    articleContent: "— Där offra de levande fåglar; runt omkring sticka de ner pilar i marken, andra också bröd och kött och vad var och en just har, som det är den härskande sedan hos dem. De kasta också lott om fåglarna, om de skola slakta dem eller uppäta dem eller låta dem vara i livet.För ungefär tusen år sen skrevs detta av en grekisk kejsare om en märklig fågelkult som utövades av svenskarna i Ryssland under deras färder på sina flottar nedför Dnjepr till Konstantinopel. Offret skedde vid en jättestor ek på en ö i Dnjepr, sedan resenärerna passerat de sju forsarna vid flodens nedre lopp.Det inte minst märkliga i saken är att vi äga fynd från Gotland, som utgör en arkeologisk parallell till denna skildring, enl. vad professor Birger Nerman berättar i senaste häft. av Fornvännen.Mellan de på ett avstånd av något mer än 1 kilometer från varandra belägna gårdarna Bjerges och Gudings i Vallstena ligga de s. k. Gudingsåkrarna. Fordom var här en sank löväng. Denna har sträckt sig söderut nedåt Källunge myr, och tydligen i samband med myrens utdikning har ängsmarken dränerats.Spjutspetsarna stuckos ner kring den heliga eken.Under långa tider ha åkrarna vid Gudings givit fynd, och enbart sedan 1920 ha hundratals föremål kommit i dagen, huvudsakligen järnvapen och bland dem till övervägande del spjutspetsar. Till största delen härröra fynden från tiden omkring år 800 och 800-talet, men även från 900-talet. Dr John Nihlén har tidigare konstaterat att en stenlagd forntida väg går fram över fyndplatsen. Fynden synas främst gruppera sig kring en stor, oregelbunden vattensamling, vars närmast angränsande. med buskage beväxta område ännu är ouppodlat. Nihlén utförde här grävningar, varvid en del typiska fynden gjordes. Fynden lågo i myll-lagrets bot t, u på gränsen till den underliggande sanden, och voro lätt skönjbara. Någon bestämd orientering av föremålen kunde ej iakttagas, men de flesta spjutspetsarna, där läget kunde iakttagas, lågo med spetsarna nedåt jorden. Föremålen, som voro begränsade till vissa stråk,ha lagts eller stuckits ner i jorden utefter en smal remsa på vissa smärre fläckar. I det med buskar beväxta området närmst vattensamlingen gjordes inga fynd.Att det här rör sig om offerfynd kan ej gärna dras i tvivelsmål. Detta framgår redan av fyndens fördelning och av att spjutspetsarna till stor del tydligt nedstuckits. Ängsmarkerna låga ju också nedåt Källunge myr och offer ha ju under forntiden ofta försiggått i eller intill myrar eller små insjöar.Här intresserar närmast ett bestämt fynd. Enligt uppgifter, som Nihlén erhöll, hade år 1920 ett stycke sydväst om vattensamlingen den dåvarande ägaren av Lilla Gudings gjort ett fynd som bestått av 8 stycken långa spjutspetsar. nedstuckna i jorden lodrätt med spetsarna ner och bildande en ring med cirka 2,5 meters diameter. Ejderhansblotarnas offerplats.Hos professor Nerman väcka de i den gotländska offerjorden nedstuckna spjutspetsarna tanken på de pilar som svenskarna i Ryssland stucko ned i jorden kring den heliga eken på S:t Gregot i Dnjepr. Spjutspetsrins diameter uppges ha varit c m., vilket mycket väl skulle passa om spjuten just stått nedstuckna runt ett träd i den löväng, som kan antagas ha funnits här redan under vikingatiden.Hittills ha tyvärr icke tillräckliga undersökningar ägt ruin på Gudingsåkrarna — en ingående utgrävning vore i hög grad önskvärd, anser prof. Nerman — och därför är det ej så lätt att säga vad det är för ett slags offer som bragts gudomen på denna kultplats.Att här förekommit fågeloffer av ??????????????????? Slutsatsen gör prof. Nerman av ortnamnet som betyder ejderhane. Ortnamnet Gudings är alltså uppkallat efter gudingarna, d., v. s. ejderhansblotarna, och betyder helt enkelt ejderhansblotarnas offerplats.Även i Eke finns en gård med namnet Gudings och även där har påträffats offerfynd, bestående av ett antal bronshalsringar från tiden omkring år 500. Alldeles intill befinner sig också här en liten myr.",
